<script>
import JSZip from 'jszip'
import DownloadUtil from '@/util/DownloadUtil'
import FileUtil from '@/util/FileUtil'

export default {
  name: 'ImageSlicerView',
  data() {
    return {
      originalImage: null,
      imageInfo: {
        width: 0,
        height: 0,
        size: 0
      },
      sliceMode: 'count', // count: æŒ‰æ•°é‡, height: æŒ‰é«˜åº¦
      sliceCount: 4,
      sliceHeight: 500,
      outputFormat: 'original',
      jpgQuality: 85,
      slicePreviews: [],
      isProcessing: false,
      canvas: null,
      ctx: null
    }
  },
  mounted() {
    this.canvas = document.createElement('canvas')
    this.ctx = this.canvas.getContext('2d')
  },
  methods: {
    handleImageUpload(file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        const img = new Image()
        img.onload = () => {
          this.originalImage = e.target.result
          this.imageInfo = {
            width: img.width,
            height: img.height,
            size: file.size
          }
          this.updatePreview()
        }
        img.src = e.target.result
      }
      reader.readAsDataURL(file)
      return false // é˜»æ­¢è‡ªåŠ¨ä¸Šä¼ 
    },

    updatePreview() {
      if (!this.originalImage) return

      const img = new Image()
      img.onload = () => {
        this.generateSlices(img)
      }
      img.src = this.originalImage
    },

    generateSlices(img) {
      const slices = []
      const { width, height } = img
      
      let sliceHeight
      let sliceCount
      
      if (this.sliceMode === 'count') {
        sliceCount = this.sliceCount
        sliceHeight = Math.floor(height / sliceCount)
      } else {
        sliceHeight = this.sliceHeight
        sliceCount = Math.ceil(height / sliceHeight)
      }

      for (let i = 0; i < sliceCount; i++) {
        const y = i * sliceHeight
        const currentHeight = Math.min(sliceHeight, height - y)
        
        if (currentHeight <= 0) break

        // è®¾ç½®canvaså°ºå¯¸
        this.canvas.width = width
        this.canvas.height = currentHeight

        // ç»˜åˆ¶åˆ‡ç‰‡
        this.ctx.drawImage(img, 0, y, width, currentHeight, 0, 0, width, currentHeight)

        // è·å–æ ¼å¼å’Œè´¨é‡
        let format = this.outputFormat === 'original' ? this.getOriginalFormat() : `image/${this.outputFormat}`
        let quality = format === 'image/jpeg' ? this.jpgQuality / 100 : undefined

        const dataUrl = this.canvas.toDataURL(format, quality)
        
        // è®¡ç®—æ–‡ä»¶å¤§å°ï¼ˆä¼°ç®—ï¼‰
        const base64Length = dataUrl.split(',')[1].length
        const size = Math.round(base64Length * 0.75)

        slices.push({
          dataUrl,
          width,
          height: currentHeight,
          size,
          index: i + 1
        })
      }

      this.slicePreviews = slices
    },

    getOriginalFormat() {
      // ä»åŸå›¾URLåˆ¤æ–­æ ¼å¼
      if (this.originalImage.includes('data:image/png')) return 'image/png'
      if (this.originalImage.includes('data:image/webp')) return 'image/webp'
      return 'image/jpeg'
    },

    async downloadAll() {
      if (this.slicePreviews.length === 0) return

      this.isProcessing = true
      
      try {
        const zip = new JSZip()
        const folder = zip.folder('image-slices')

        for (let i = 0; i < this.slicePreviews.length; i++) {
          const slice = this.slicePreviews[i]
          const base64Data = slice.dataUrl.split(',')[1]
          const extension = this.getFileExtension()
          
          folder.file(`slice-${String(i + 1).padStart(2, '0')}.${extension}`, base64Data, { base64: true })
        }

        const content = await zip.generateAsync({ type: 'blob' })
        DownloadUtil.downloadBlob(content, 'image-slices.zip')
        
        this.$message.success(`æˆåŠŸæ‰“åŒ… ${this.slicePreviews.length} ä¸ªåˆ‡ç‰‡`)
      } catch (error) {
        console.error('æ‰“åŒ…å¤±è´¥:', error)
        this.$message.error('æ‰“åŒ…å¤±è´¥ï¼Œè¯·é‡è¯•')
      } finally {
        this.isProcessing = false
      }
    },

    downloadSingle() {
      this.slicePreviews.forEach((slice, index) => {
        const extension = this.getFileExtension()
        const filename = `slice-${String(index + 1).padStart(2, '0')}.${extension}`
        DownloadUtil.downloadImage(slice.dataUrl, filename)
      })

      this.$message.success(`å¼€å§‹ä¸‹è½½ ${this.slicePreviews.length} ä¸ªåˆ‡ç‰‡`)
    },

    getFileExtension() {
      if (this.outputFormat === 'original') {
        if (this.originalImage.includes('data:image/png')) return 'png'
        if (this.originalImage.includes('data:image/webp')) return 'webp'
        return 'jpg'
      }
      return this.outputFormat === 'jpg' ? 'jpg' : this.outputFormat
    },



    resetAll() {
      this.originalImage = null
      this.imageInfo = { width: 0, height: 0, size: 0 }
      this.slicePreviews = []
      this.sliceMode = 'count'
      this.sliceCount = 4
      this.sliceHeight = 500
      this.outputFormat = 'original'
      this.jpgQuality = 85
    },

    formatFileSize(bytes) {
      return FileUtil.formatBytes(bytes)
    }
  }
}
</script>

